@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

* {
	box-sizing: border-box;
}

body {
	background-image: url('img/bg-main.jpg');
	background-repeat: no-repeat;
	background-position: left;
	background-size: cover;
    font-family: 'Lato', sans-serif;
}

header {
    display: flex;
    justify-content: center;
}

nav {
    overflow: hidden;
    display: inline-flex;
    justify-content: center;
}

nav li {
    list-style-type: none;
    float: left;
}

nav a {
    float: left;
    color: #fff;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
	opacity: 0.8;
	transition: 0.3s;
	border-radius: 30px;
	display: block;
	padding: 10px;
    margin-right: 10px;
}

nav a:hover, nav a.active {
	opacity: 1;
	background-color: #4792C4;
}

.fullscreen {
    margin: 20px 15%;
    background-color: rgba(255,255,255,0.9);
    border-radius: 10px;
    padding: 10px 30px;
    text-align: justify;
}

.top-right {
    font-size: 12px;
    float: right;
}

.imgright {
    max-width: 100%;
}

@media (min-width: 800px) {
    .imgright {
        max-width: 40%;
        float: right;
        clear: right;
        margin: 0px 20px;
    }

    .imgleft {
        max-width: 40%;
        float: left;
        clear: left;
        margin: 0px 20px;
    }
}